body {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #1c1c1e;
  background-image: linear-gradient(to right, #1c1c1e, #363636);
  color: white;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  max-width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  height: 100vh; /* 画面全体を使用 */
}

.menu-section-container {
  flex: 1; /* 上半分 */
  overflow-y: auto; /* メニューが多い場合にスクロール */
  padding: 0px;
  box-sizing: border-box;
}

.menu-section {
  margin: 10px 0;
  animation: fadeIn 1s ease-in-out;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  padding: 2px;
  border-radius: 8px;
  border: 1px solid rgb(59, 59, 59);
  background-color: #2c2c2e;
  transition: background-color 0.2s ease;
}

.menu-item:hover {
  background-color: #3a3a3c;
  /* background-color: #ff3b30; */
}

.item-info {
  display: flex;
  flex-direction: column;
  flex: 2;
}

.item-name {
  font-size: 20px;
  margin-bottom: 0px;
  margin-left: 3px;
  color: #f5f5f5;
}

.item-price {
  font-size: 18px;
  margin-bottom: 0px;
  margin-left: 3px;
  color: #bbbbbb;
}

.item-select {
  max-width: fit-content;
  flex: 1;
  padding: 5px;
  font-size: 18px;
  margin-right: 0px;
  text-align: center;
  border: 1px solid #5e5e5e;
  border-radius: 5px;
  background-color: #333333;
  color: #f5f5f5;
}

.item-total {
  flex: 1;
  font-size: 20px;
  text-align: right;
  color: #f5f5f5;
}

.footer {
  flex: 0.4; /* 下半分 */
  background-color: transparent;
  padding: 10px;
  border-top: 1px solid #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  z-index: 40;
}

.totals {
  width: 100%;
  text-align: left;
  margin-bottom: 10px;
}

.section-total, .grand-total {
  font-size: 18px; /* 合計金額のサイズ調整 */
  margin: 0px 0;
  color: #f5f5f5;
}

.grand-total {
  max-width: fit-content;
  font-size: 24px; /* 合計金額のサイズ調整 */
  color: #00d4ff;
}

.clear-button {
  background-color: #ff3b30;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 18px;
  cursor: pointer;
  width: 100%;
  text-align: center;
  border-radius: 10px;
  margin-top: 10px;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.clear-button:hover {
  background-color: #ff453a;
  transform: scale(1.05);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* モバイル専用のスタイル */
@media (max-width: 600px) {
  .App {
    padding: 10px;
  }

  h1, h2, h3 {
    font-size: 30px;
  }

  h2 {
    text-shadow: 2px 2px 3px rgb(60, 60, 60);
    text-decoration: underline;
  }

  .item-name {
    font-size: 18px;
  }

  .item-price, .item-total {
    font-size: 18px;
  }

  /* .section-total, .grand-total { */
    /* font-size: 18px; モバイルでも少し小さく */
  /* } */

  .clear-button {
    font-size: 18px;
    padding: 10px 20px;
  }
}