body {
  font-family: Arial, sans-serif;
  background-color: #121212;
  color: white;
  padding: 20px;
}

.App {
  max-width: 500px;
  margin: 0 auto;
}

h1 {
  font-size: 24px;
  text-align: center;
}

h2 {
  font-size: 20px;
  margin-top: 20px;
}

h3 {
  font-size: 18px;
}

div {
  margin-bottom: 15px;
}

input {
  margin-left: 10px;
  padding: 5px;
  width: 50px;
  text-align: center;
  border-radius: 5px;
  border: 1px solid #ccc;
}

span {
  font-size: 16px;
}